import axios from "axios";
import React, { useEffect, useState, useContext } from "react";
import { AppConfig } from "../../config/config";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../provider/loginProvider";

const ChatRoomList = () => {
    
    const navigator = useNavigate();

    const [roomList, setRoomList] = useState([]);
    const [chatInfo, setChatInfo] = useState({
        name : "",
        comment : "",
        constraint : "",
        password : ""
    });

    /**call roomList */
    useEffect(()=>{
        const getRoomList = async()=>{
            const response =  await axios.get(AppConfig.serverAddress + "/api/chat/roomList");
            if(response.data){ 
                setRoomList(response.data.roomList);
            }
        }
        getRoomList();
    },[])

    
    /** submit new Room */
    const addNewRoom =  async (e) => {
        e.preventDefault();
        const response = await axios.post(AppConfig.serverAddress + "/api/chat/addRoom", chatInfo, {
            headers: {
              "Content-Type": "application/json",
            },
          });
    }

    /** chat infomation change process */
    const chatInfoChange = (e) => {
        const { name, value } = e.target;
         setChatInfo({
        ...chatInfo,
        [name]: value,
        });
    };

    /** room entry proccess */
    const enterRoom = (e) => {
        const roomId = e.target.getAttribute("data-id");
        const selectedRoom = roomList.find((room) => room._id === roomId);
        navigator("/chat", {
            state : selectedRoom
        });
    }

    return(
        <div>
            <h3> </h3>
                <ul>
                    {roomList.map((room, idx) => (
                        <li key={idx}>{room.name}<button data-id={room._id} onClick={enterRoom}></button></li>    
                    ))}
                </ul>
                <form onSubmit={addNewRoom}>
                    <p>  </p>
                    <input type="text" name="name" placeholder="" onChange={chatInfoChange} />
                    <input type="text" name="comment"  value=" " onChange={chatInfoChange}/>
                    
                    <p> </p>
                    <input type="radio" onClick={chatInfoChange}  name="constraint" value="true" />
                    <input type="radio" onClick={chatInfoChange} name="constraint" value="false" /> 
                    {chatInfo.constraint == 'true' ? ( <input type="password" name="password" placeholder="" onChange={chatInfoChange} />) : null}
                    <button type="submit"> </button>
                </form>
        </div>
    )

}

export default ChatRoomList;